import React from "react";
import StepsWaring from "./StepsWaring";
import bg from "../assets/Background-Form.jpg";

const selectTable = [
  "ubrania, ktre nadaj si do ponownego uycia",
  "ubrania, do wyrzucenia",
  "zabawki",
  "ksiki",
  "Inne",
];

const Step1 = ({ formValues, setFormValues, setFormStep }) => {
  return (
    <section className="steps">
      <StepsWaring>
        <h4>Wane</h4>
        <p>
          Uzupenij szczegy dotyczce Twoich rzeczy. Dziki temu bdziemy
          wiedzie komu najlepiej je przekaza.
        </p>
      </StepsWaring>
      <div
        className="steps-container"
        style={{ backgroundImage: `url(${bg})` }}
      >
        <div className="steps-content">
          <h5>Krok 1/4</h5>
          <h3>Zaznacz co chcesz odda:</h3>
          <div className="select-container--1">
            {selectTable.map((select, index) => (
              <div
                key={index + 1254}
                className="select-element"
                onClick={() =>
                  setFormValues((prev) => ({ ...prev, whoToGiveBack: select }))
                }
              >
                <span
                  style={{
                    backgroundColor:
                      formValues.whoToGiveBack === select
                        ? "#FAD648"
                        : "transparent",
                  }}
                ></span>
                {select}
              </div>
            ))}
          </div>
          <div className="steps-control">
            {formValues.whoToGiveBack && (
              <div
                to="step-2"
                className="controler"
                onClick={() => setFormStep(2)}
              >
                Dalej
              </div>
            )}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Step1;
